import { Hadidja, Logo, Sourse, Video, VideoLarge, VideoMain, VideoSmall, WrapperHeader, WrapperVideo } from "./Header.styled";

const Header = ({ screen, showSidebar }) => <WrapperHeader   onClick={() => showSidebar(false)}>
      <Logo>
        <img src={screen.crown} alt='Hadidja sweets' />
      </Logo>
      <WrapperVideo>
        <Video>
          <VideoSmall autoPlay loop muted playsinline src={screen.v2}>
          </VideoSmall>
        </Video>
        <VideoMain>
          <VideoLarge autoPlay loop muted playsinline src={screen.v1}>
          </VideoLarge>
        </VideoMain>
        <Video>
          <VideoSmall autoPlay loop muted playsinline src={screen.v3}>
          </VideoSmall>
        </Video>
      </WrapperVideo>
      <Hadidja>
        <img src={screen.hadidja} alt="Hadidja Sweets" />
      </Hadidja>
    </WrapperHeader>

export default Header